import React from 'react';
import './styles/Message.css'
import VerticalTrait from "../Assets/Buttons/trait_vertical.png";

export default class Message extends React.Component{
    constructor(props){
        super(props);
    }

    render() {
        return(
            <div>
                <div id="MessageComponent">
                    <img className={"VerticalTraitMessage"} src={VerticalTrait}/>
                    <h1 id={"h1IDMessageComponent"}>{this.props.title}</h1>
                    <p id={"pIDMessageComponent"}> &emsp; &emsp; {this.props.message} <br/> <b> Pour en savoir plus, cliques sur la map... ;)</b> </p>
                </div>
            </div>
        )
    }
}
